<template>
  <main class="flex-1 relative z-0 overflow-y-auto focus:outline-none" tabindex="0">
    <nav class="bg-white sticky top-0 z-10 shadow">
      <div class="mx-auto px-6">
        <div class="h-14 flex justify-between">
          <div class="hidden sm:flex">
<!--            <a-->
<!--            class="inline-flex items-center font-mono uppercase text-sm pt-1 border-b-3 transition duration-200 ease-in hover:no-underline focus:outline-none text-gray-600 border-transparent hover:border-primary hover:text-primary " :class="{'text-primary border-primary':toRequestStatus}" @click="toRequest">Requests</a>-->
            <a
            class="inline-flex items-center font-mono uppercase text-sm pt-1 border-b-3 transition duration-200 ease-in hover:no-underline focus:outline-none text-gray-600 border-transparent hover:border-primary hover:text-primary ml-6" :class="{'text-primary border-primary':!toRequestStatus}"
            @click="toProsetting">{{$t('settings')}}</a></div>
        </div>
      </div>
    </nav>
    <router-view></router-view>
  </main>
</template>

<script>
export default {
  name: 'detail',
  data() {
    return {
      toRequestStatus: true,
      item: {
        project_id: null,
        project_secret: null,
        urls: null,
        project_name: null,
      },

    };
  },
  methods: {
    toRequest() {
      this.$router.push(`/dashboard/detail/request/${this.api_key}/${this.api_secret}/${this.id}`);
    },
    toProsetting() {
      // this.$router.push(`/dashboard/detail/prosetting/${this.api_key}/${this.api_secret}/${this.id}`);
      this.$router.push({ path: '/dashboard/detail/prosetting', query: { item: this.item } });
    },
  },
  watch: {
    $route(to) {
      this.toRequestStatus = (to.path.indexOf('setting') === -1);
    },
  },
  mounted() {
    const to = this.$route;
    this.toRequestStatus = (to.path.indexOf('setting') === -1);

    const {
      project_id, project_name, project_secret, urls,
    } = this.$route.query.item;
    this.item.project_id = project_id;
    this.item.project_name = project_name;
    this.item.project_secret = project_secret;
    this.item.urls = urls;
  },
};
</script>

